import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";

import Home from './components/Home';
import About from './components/About';

import StardogStudio from './components/StardogStudio';
import Studao from './components/Studao';
import FourOFour from './components/FourOFour';

import MainNav from './components/MainNav';
import ScrollToTop from './components/ScrollToTop'
import Loader from './components/Loader';
import Footer from './components/Footer';

import { CSSTransition } from 'react-transition-group';


import "animate.css/animate.min.css";
import './styles/main.scss'


function App() {
  return (
    <Router>
      <MainNav />
      
      <ScrollToTop />
      <Switch>
        <Route exact path="/">
          <Loader />
          <Home />
        </Route>

        <Route path="/desiging-ux-in-a-b2b-environment-for-a-technical-audience-stardog-studio">
          <StardogStudio />
        </Route>

        <Route path="/designing-an-online-learning-platform-for-independent-filmmakers">
          <Studao />
        </Route>

        <Route path="/about">
          <About />
        </Route>

        <Route component={FourOFour} />

      </Switch>

      <Footer />
    </Router>
    
  );
}

export default App;
